.header {
    background-color: #59595a;
    padding: 5px;
    padding-bottom: 20px;
}

.header > *:nth-child(2) {
    flex: 1;
    font-size: 16px;
    text-transform: uppercase;
    color: white;
}

.header > *:last-child {
    background-image: url('./quote.png');
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: 0px 5px;
    width:20px;
    height: 25px;
}